@font-face {
    font-family: SansProRegular;
    src: url(Source_Sans_Pro/SourceSansPro-Regular.ttf);
}
@font-face {
    font-family: SansProBold;
    src: url(Source_Sans_Pro/SourceSansPro-Bold.ttf);
}
@font-face {
    font-family: Leelawadb;
    src: url(Leelawad/leelawdb.ttf);
}

p {
    margin: 1.5vw;
}
ul {
    margin: 0;
}
:root {
    --main-color: #0099FF;
}

.main-content-container {
}

.first-section-cont {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    height: 100vh;
}
.second-section-cont {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: row;
    height: 100vh;
    background-color: var(--main-color);
}
.third-section-cont {
    display: flex;
    align-items: center;
    justify-content: center;
    padding-left: 5vmin;
    padding-right: 5vmin;
    flex-direction: column;
    color: var(--main-color);
    font-size: 1.2vw;
    padding-top: 2vw;
    padding-bottom: 5vh;
}
.main-logo {
    height: 38vmin;
}
.sub-title {
    font-family: SansProBold;
    color: var(--main-color);
    font-size: 5.8vmin;
    margin: 0;
}
.buttons-row-start {
    display: flex;
    align-items: center;
    justify-content:space-between;
    width: 31vw;
    margin-top: 2vh;
}
.blue-round-button {
    padding: 0.5vw 1vw 0.5vw 1vw;
    border: 0.2vh solid var(--main-color);
    background-color: var(--main-color);
    color: white;
    width: 32%;
    text-align: center;
    border-radius: 1.5rem;
    font-size: 1vw;
}
.outlined-button {
    padding: 0.5vw 1vw 0.5vw 1vw;
    border: 0.2vh solid var(--main-color);
    color: var(--main-color);
    width: 20%;
    text-align: center;
    border-radius: 1.5rem;
    font-size: 1vw;
}
.links-row {
    display: flex;
    flex-direction: row;
    width: 25vmax;
    justify-content: space-between;
    margin-top: 2.5vmax;
}
.row-icons {
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 1vmax;
}
.margin-icons {
    margin-right: 1vw;
    height: 3vh;
}
.aligned-download-btn {
    display: flex;
    align-items: center;
    justify-content: center;
}
.download-arrow {
    width: 1.1vw;
    margin-left: 1vw
}
.word-logo {
    font-family: Leelawadb;
    font-size: 10.2vmax;
    font-weight: bold;
    text-align: center;
    margin: 0;
    line-height: 20vmin;
    margin-top: 1vw;
    color: var(--main-color);
}
.background-wave-logo {
    position: absolute;
    height: 100vh;
    width: 100vw;
    z-index: -1;
}
.background-wave-logo-small {
    display: none;
}
.sec-left-section {
    flex: 1;
    color: white;
    font-size: 1.2vw;
    padding-left: 5vmin;
    padding-right: 5vmin;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    font-family: SansProRegular;
    
}
.sec-right-section {
    flex: 1;
    height: 100vh;
    position: relative;
}
.list-icon {
    display: flex;
    align-items: center;
}
.list-icons-svg {
    margin-right: 1vw;
    height: 4vh;
}
.spotlight-one {
    position: absolute;
    bottom: 0;
    background-color: var(--main-color);
}
.spotlight-one-small {
    display: none;
}
.spotlight-two-small {
    display: none;
}
.title-bold {
    color: var(--main-color);
    font-family: SansProBold;
    text-align: center;
    font-size: 2vw;
}
.ul-padding {
    padding: 0 4vw 0 4vw;
}
.ul-padding-inner{
    padding: 0 2vw 0 2vw;
}
.text-underline {
    text-decoration: underline;
}
.row-download-icons {
    display: flex;
}
.icon-container-download {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    color: var(--main-color);
}
.download-os {
    width: 1.5vw;
    margin-bottom: 0.5vw
}
.middle-download-logo {
    margin-left: 1.5vw;
    margin-right: 1.5vw;
}
@media only screen and (max-width: 600px) {
    .links-row {
        display: flex;
        flex-direction: row;
        width: 90vw;
        justify-content: space-between;
        margin-top: 6vmax;
    }
    .row-icons {
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 2vh;
    }
    .margin-icons {
        margin-right: 1.5vh;
        height: 3vh;
    }
    .buttons-row-start {
        display: flex;
        align-items: center;
        justify-content:space-between;
        width: 90vw;
        margin-top: 6vh;
    }
    .blue-round-button {
        padding: 0.5vw 1vw 0.5vw 1vw;
        border: 0.2vh solid var(--main-color);
        background-color: var(--main-color);
        color: white;
        width: 32%;
        text-align: center;
        border-radius: 1.5rem;
        font-size: 1vw;
        display: none;
    }
    .outlined-button {
        padding: 0.5vw 1vw 0.5vw 1vw;
        border: 0.2vh solid var(--main-color);
        color: var(--main-color);
        width: 45%;
        text-align: center;
        border-radius: 1.5rem;
        font-size: 6vw;
    }
    .colored-small {
        background-color: var(--main-color);
        color: white
    }
    .main-logo {
        height: 60vmin;
        margin-top: -5vh;
    }
    .background-wave-logo {
        display: none;
    }
    .background-wave-logo-small {
        position: absolute;
        height: 100vh;
        width: 100vw;
        z-index: -1;
        display: block;
    }
    .sec-right-section {
        display: none;
    }
    .sec-left-section {
        font-size: 4vw;
        padding-left: 2vmin;
        padding-right: 3vmin;
        justify-content: space-evenly;
        font-family: SansProRegular;
    }
    .list-icons-svg {
        margin-right: 2vw;
        height: 4vh;
    }
    .spotlight-one-small {
        display: block;
        height: 40vh;
        position: relative;
        background-color: var(--main-color);
    }
    .spotlight-two-small {
        display: block;
        height: 50vh;
        position: relative;
        background-color: var(--main-color);
    }
    .third-section-cont {
        font-size: 4vw;
        padding-top: 2vw;
        padding-bottom: 10vw;
    }
    .title-bold {
        font-size: 7vw;
        margin-bottom: 4vw;
    }
}
.footer {
    padding-bottom: 10px;
    padding-top: 10px;
    text-align: center;
}